@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  padding: 2rem;
  width: 50rem;
  color: white;
  background-color: $colorBgMediaLighter;

  .buttonClose {
    @include resetButton;

    position: absolute;
    top: -5rem;
    right: 0;
  }

  .icon {
    width: 3rem;
    fill: $colorHighlightLight;
  }

  .formWrapper {
    margin-top: 3rem;
  }

  .input {
    &:not(:first-child) {
      margin-top: 2rem;
    }
  }

  .buttonSubmit {
    display: block;
    margin: 2rem auto 0 auto;
  }
}
