@import "styles/mixins";
@import "styles/variables";

.header {
  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 1.3rem;
    padding-bottom: 1.3rem;
  }

  .title {
    @include h2($colorFontLightest);

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0;

    span {
      @include fontMedium($colorFontMedium);

      margin-top: 1rem;
      width: 15rem;
    }
  }

  .type,
  .clinic,
  .date {
    @include fontMedium($colorFontMedium);

    margin: 1.2rem 0 0 0;

    span {
      font-weight: 900;
    }
  }
}
