@import "styles/variables";
@import "styles/mixins";

.container {
  display: flex;
  flex-direction: column;
}

.wrapper {
  display: flex;
  justify-content: space-between;
}

.buttonSetup {
  @include resetButton();

  margin-left: auto;
  padding: 0.5rem;

  &:hover {
    .icon {
      transform: rotate(360deg);
    }
  }
}

.icon {
  width: 3.5rem;
  fill: $colorHighlightLight;
  transition-duration: 1s;
}

.count {
  @include h4($colorFontLightest);

  display: inline-block;
  margin: 0 0 0;
  padding: 1rem;
  background-color: $colorHighlightDark;
}

.name {
  @include h4($colorHighlightLight);

  margin: 0;
  text-align: right;
}
