@import "styles/variables";
@import "styles/mixins";

.input {
  position:absolute;
  top:50%;
  margin:0;
  padding:0;
  width:10px;
  height:10px;
  border:0;
  opacity:0;

  & + label {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;

    &::after {
      content: "";
      width: 2rem;
      height: 2rem;
      background-color: transparent;
      border: 1px solid $colorHighlightLight;
      border-radius: 0.2rem;
      transition-duration: $transitionDuration;
    }

    span {
      transition-duration: $transitionDuration;
    }

    &:hover {
      span{
        opacity: 0.5;
      }
    }

    .iconCheck {
      position: absolute;
      top: 1.2rem;
      right: 0.9rem;
      width: 1rem;
      pointer-events: none;
      opacity: 0;
      transition-duration: $transitionDuration;
    }
  }

  &:checked + label {
    &::after {
      background-color: $colorHighlightLight;
    }

    .iconCheck {
      stroke: $colorFontLightest;
      opacity: 1;
    }
  }
}
