@import "styles/variables";
@import "styles/mixins";

.wrapper {
  margin-top: -2rem;
  padding-bottom: 3rem;

  .report {
    background-color: $colorHighlightDark;

    &:hover {
      background-color: $colorHighlightDark;
      opacity: 0.7;
    }
  }

  .comment {
    margin-top: 1rem;
  }
}

.formWrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}

.list {
  @include resetList;

  margin: 0;
}

.check {
  @include fontCalendarDays($colorFontLightest);

  margin-top: 1rem;
}

.controls {
  margin-top: 3rem;
}

.button {
  &:not(:first-child) {
    margin-left: 2rem;
  }
}
